.Header {
    background-color: red;
    color: black;
}

.Logo {
    display: inline;
}
.button-panel {
    border: 1px solid white;
    vertical-align: middle;
}

.Header input[type="button"] {
    padding: 10px;
    border-radius: 10%;
    text-transform: uppercase;
    border: 0.5px solid white;
    margin: 5px 5px;
    background-color: #353232;
    color: whitesmoke;
    transition: 0.1s;
    width: 90px;
    margin-right: 20px;
    display: block;
    margin: auto;
}

.Header input[type="button"]:hover {
    background-color:red;
    color: black;
    border: none;
}
