* {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
body {
    color: whitesmoke;
    background-color: #121212;
}

.ImagePanel {
    width: 80vw;
    margin: auto;
    /* change top margin here */
    margin-top: 100px;
    display: flex;
    flex-direction: row;
    /* flex-basis: 400px; */
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    /* border: 1px solid red; */
 }

.ImageComponent {
    width: 300px;
    margin-bottom: 10px;
    transition: 0.3s;
    cursor: pointer;
    background-color: #2b2828;
    border-radius: 2%;
    text-transform: lowercase;
    font-weight: bolder;
    box-shadow: 0px 2px rgb(77, 71, 71);;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.ImageComponent:hover, .ImageComponent:hover img {
    width:310px;
    box-shadow: none;
    color: black;
    background-color: red;
}
.Image img {
    width: 300px;
    cursor: pointer;
    transition: 0.5s;
}

.ImageDescription {
    text-align: center;
    margin: 10px 10px;
}

